<template>
	<view id="app">
		<!-- Ëá™ÂÆö‰πâTabBarÁªÑ‰ª∂ -->
		<custom-tabbar 
			ref="customTabbar"
			:selected="selectedTabIndex"
			:show-icon="true"
			direction="horizontal"
			@tabChange="onTabChange"
		/>
	</view>
</template>

<script>
import { ref, onMounted } from 'vue'
import CustomTabbar from './components/custom-tabbar/custom-tabbar.vue'

export default {
	components: {
		CustomTabbar
	},
	setup() {
		// ÂΩìÂâçÈÄâ‰∏≠ÁöÑtabÁ¥¢Âºï
		const selectedTabIndex = ref(0)
		const customTabbar = ref(null)

		// TabÂàáÊç¢‰∫ã‰ª∂
		const onTabChange = (index, item) => {
			selectedTabIndex.value = index
			console.log('TabÂàáÊç¢:', index, item)
		}

		// Â∫îÁî®ÁîüÂëΩÂë®Êúü
		onMounted(async () => {
			// Âä®ÊÄÅÂØºÂÖ•tabbarÂ∑•ÂÖ∑
			const { default: customTabBar } = await import('./utils/tabbar.js')
			
			// Ê≥®ÂÜåTabBarÂÆû‰æãÂà∞Â∑•ÂÖ∑Á±ª
			if (customTabbar.value) {
				customTabBar.setInstance(customTabbar.value)
			}
			
			console.log('AppÂêØÂä®ÔºåTabBarÂàùÂßãÂåñÂÆåÊàê')
		})

		return {
			selectedTabIndex,
			customTabbar,
			onTabChange
		}
	},
	
	onLaunch: function() {
		console.log('App Launch')
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
</script>

